&:hover {\n                    i,\n                    span {\n                        color: $primary;\n                    }\n                }\n            }\n\n            &:last-child {\n                border-right: 0;\n            }\n\n            &.app-switcher-item {\n                .header-apps {\n                    width: 230px;\n                    padding: 0 0 10px 0;\n\n                    .ivu-col {\n                        padding: 10px 0;\n\n                        a {\n                            display: flex;\n                            flex-direction: column;\n                            border-radius: 3px;\n                            height: 90px;\n                            width: 75px;\n                            color: #737373;\n                            align-items: center;\n                            justify-content: center;\n                            border: 1px solid transparent;\n\n                            &:hover {\n                                border: 1px solid #eeeeee;\n                            }\n\n                            img {\n                                height: 35px;\n                                max-height: 35px;\n                                max-width: 35px;\n                                margin-bottom: 8px;\n                            }\n\n                            span {\n                                display: block;\n                                overflow: hidden;\n                                text-overflow: ellipsis;\n                                white-space: nowrap;\n                                font-size: 12px;\n                                color: #555555;\n                            }\n                        }\n                    }\n                }\n            }\n\n            &.paper-notification {\n                padding: 0 8px;\n\n                a {\n                    .ivu-badge {\n                        .ivu-badge-count {\n                            t