ing: 6px 12px;\n                                    display: flex;\n                                    flex-direction: column;\n\n                                    &:hover {\n                                        background: #f4f5f6;\n                                    }\n\n                                    .notif-title {\n                                        color: $primary;\n                                        white-space: normal;\n                                    }\n\n                                    .notif-content {\n                                        display: flex;\n\n                                        a {\n                                            flex: 1;\n                                            color: #555555;\n                                        }\n\n                                        span {\n                                            margin-left: auto;\n                                            font-size: 10px;\n                                            color: #777777;\n                                        }\n                                    }\n                                }\n                            }\n                        }\n\n                        .no-notifs {\n                            height: 200px;\n                            display: flex;\n                            align-items: center;\n                            justify-content: center;\n                            text-align: center;\n                            color: $primary;\n                        }\n\n                        .all-notif {\n                            display: flex;\n                            border-top: solid 1px #dedede;\n                            color: $primary;\n                            align-items: center;\n                            justify-content: flex-end;\n                            padding: 8px 12px;\n                            cursor: pointer;\n                            font-weight: 500;\n\n                            span {\n                                margin-right: 5px;\n                            }\n\n                            &:hover {\n                                color: $primary;\n                            }\n                        }\n                    }\n                }\n            }\n\n            &.avatar-item {\n                .avatar {\n                    padding: 0;\n                    display: flex;\n                    align-items: center;\n                    img {\n                        max-height: 40px;\n                        max-width: 40px;\n                        border-radius: 50%;\n                        margin: 0 10px 0 5px;\n                        border: solid 1px transparent;\n                    }\n\n                    .avatar-name{\n                        display: flex;\n                        flex-direction: column;\n                        align-items: flex-start;\n                        justify-content: center;\n                        height: 100%;\n                    }\n\n                    span {\n                        color: $text-color;\n                        font-size: 11px;\n                        text-transform: capitalize;\n                    }\n\n                    b{\n                        font-size: 12px;\n                        margin-top: -3px;\n                        font-weight: 500;\n                    }\n                    &:hover{\n                    }\n                }\n\n                .ivu-poptip-content {\n                    padding: 0;\n\n                    .ivu-poptip-arrow {\n                        border-bottom-color: $primary;\n\n                        &:after {\n                            border-bottom-color: $primary;\n                        }\n                    }\n\n                    .ivu-poptip-body {\n                        padding: 0 !important;\n                        width: 200px;\n\n                        .hea