5px;\n                        color: $primary;\n                        transition: all .3s;\n                        align-items: center;\n                        border-left: 0;\n                        border-top-right-radius: 15px;\n                        border-bottom-right-radius: 15px;\n                        width: 190px;\n                        margin: 5px 10px 0 0;\n                        i {\n                            color: $text-color;\n                            display: block;\n                            font-size: 22px;\n                            transition: all .3s;\n                            margin-right: 6px;\n                        }\n                        span:not(.ivu-badge) {\n                            color: $text-color;\n                            font-size: 11px;\n                            //font-weight: 500;\n                            line-height: 12px;\n                            padding-left: 5px;\n                            padding-right: 5px;\n                            display: block;\n                            transition: all .3s;\n                        }\n\n                        .ivu-badge {\n                            position: absolute;\n                            top: 3px;\n                            right: 10px;\n                        }\n\n                        &:hover {\n                            background: lighten($primary, 20%);\n\n                            i {\n                                color: #ffffff;\n                            }\n                        }\n                        &.active {\n                            background: lighten($primary, 5%);\n                            span, i {\n                                color: #ffffff;\n                            }\n                        }\n                    }\n                }\n            }\n        }\n    }\n\n    &.hover {\n        width: 70px !important;\n        nav {\n            ul {\n                li {\n                    padding: 0 0 1px 0;\n                    a {\n                        position: relative;\n                        display: block;\n                        text-align: center;\n                        height: $anchor-height;\n                        overflow-y: hidden;\n                        padding: 0;\n                        // border-left: 0;\n                        i {\n                            color: $text-color;\n                            display: inline-block;\n                            line-height: $anchor-height;\n                            height: $anchor-height;\n                            width: 100%;\n                            font-size: 22px;\n                            -webkit-transition: margin-top 0.2s;\n                            transition: margin-top 0.2s;\n                            margin: 0;\n                        }\n                        .ivu-badge {\n                            position: absolute;\n                            top: 3px;\n                            right: 10px;\n                        }\n                        span:not(.ivu-badge) {\n                            display: flex;\n                            align-items: center;\n                            justify-content: center;\n                            color: $text-color;\n                            height: $anchor-height;\n                            font-size: 11px;\n                            font-weight: 500;\n                            line-height: 12px;\n                            padding-left: 5px;\n                            padding-right: 5px;\n                        }\n                        &:hover {\n                            background: lighten($primary, 5%);\n                            i {\n                                color: #ffffff;\n                                margin-top: -$anchor-height;\n                            }\n                        }\n                        &.active {\n           