               color: $primary;\n                }\n            }\n        }\n\n        nav {\n            ul {\n                width: 100%;\n                li {\n                    padding: 0 0 1px 0;\n                    &.divider {\n                        height: 1px;\n                        background: lighten(#151A26, 10%);\n                        margin-top: 10px;\n                    }\n                    &.sub-title {\n                        font-size: 13px;\n                        color: lighten($primary, 40%);\n                        margin-top: 10px;\n                        span {\n                            padding-left: 10px;\n                        }\n                    }\n                    a {\n                        position: relative;\n                        overflow-y: hidden;\n                        display: flex;\n                        padding: 6px 15px;\n                        color: $primary;\n                        transition: all .3s;\n                        align-items: center;\n                        border-left: 0;\n                        width: 100%;\n                        margin: 5px 5px 0 0;\n                        i {\n                            color: $text-color;\n                            display: block;\n                            font-size: 20px;\n                            transition: all .3s;\n                            margin-right: 6px;\n                        }\n                        span:not(.ivu-badge) {\n                            color: $text-color;\n                            font-size: 14px;\n                            line-height: 14px;\n                            padding-left: 5px;\n                            padding-right: 5px;\n                            display: block;\n                            transition: all .3s;\n                        }\n\n                        &:hover {\n                            background: lighten(#151A26, 10%);\n\n                         